// reference/rrweb/packages/rrweb/src/replay/styles/style.css  
const cssStyles = `.replayer-wrapper{position:relative;overflow:auto}.replayer-wrapper>iframe{border:none}.replayer-mouse{position:absolute;width:20px;height:20px;transition:left .05s linear,top .05s linear;background-size:contain;background-position:center center;background-repeat:no-repeat;background-image:url(data:image/svg+xml;base64,);border-color:transparent}.replayer-mouse:after{content:"";display:inline-block;width:20px;height:20px;background:rgb(73,80,246);border-radius:100%;transform:translate(-50%,-50%);opacity:.3}.replayer-mouse.active:after{animation:click .2s ease-in-out 1}.replayer-mouse.touch-device{background-image:none;width:70px;height:70px;border-width:4px;border-style:solid;border-radius:100%;margin-left:-37px;margin-top:-37px;border-color:rgba(73,80,246,0);transition:left 0s linear,top 0s linear,border-color .2s ease-in-out}.replayer-mouse.touch-device.touch-active{border-color:rgba(73,80,246,1);transition:left .25s linear,top .25s linear,border-color .2s ease-in-out}.replayer-mouse.touch-device:after{opacity:0}.replayer-mouse.touch-device.active:after{animation:touch-click .2s ease-in-out 1}.replayer-mouse-tail{position:absolute;pointer-events:none}@keyframes click{0%{opacity:.3;width:20px;height:20px}50%{opacity:.5;width:10px;height:10px}}@keyframes touch-click{0%{opacity:0;width:20px;height:20px}50%{opacity:.5;width:10px;height:10px}}.session-replay-panel{width:100%;height:100%;position:relative;display:flex;flex-direction:column;overflow:hidden;background:#fff}.session-replay-content{flex:1;position:relative;overflow:auto;min-height:0;display:flex;align-items:flex-start;justify-content:center;background:#f5f5f5}.session-replay-controls{display:flex;align-items:center;gap:12px;padding:12px 16px;background:#fff;border-top:1px solid #e0e0e0;flex-shrink:0}.session-replay-play-pause{width:36px;height:36px;border:none;border-radius:4px;background:var(--color-primary,#1a73e8);color:#fff;cursor:pointer;display:flex;align-items:center;justify-content:center;padding:0;flex-shrink:0;transition:background-color .2s}.session-replay-play-pause:hover{background:var(--color-primary-dark,#1557b0)}.session-replay-play-pause:active{background:var(--color-primary-darker,#0f4a8a)}.session-replay-play-pause svg{width:20px;height:20px;fill:currentColor}.session-replay-progress-container{flex:1;position:relative;height:8px;background:#e0e0e0;border-radius:4px;cursor:pointer;overflow:hidden}.session-replay-progress-fill{position:absolute;top:0;left:0;height:100%;background:var(--color-primary,#1a73e8);border-radius:4px;transition:width .1s linear}.session-replay-time{font-size:12px;color:#666;white-space:nowrap;min-width:80px;text-align:right;font-family:monospace}@media (max-width:600px){.session-replay-controls{padding:8px 12px;gap:8px}.session-replay-play-pause{width:32px;height:32px}.session-replay-time{font-size:11px;min-width:60px}}`;

